  --muted: #f1f5f9;
  --muted-foreground: #64748b;
  --accent: #f0fdf4;
  --accent-foreground: #166534;
  --destructive: #dc2626;
  --destructive-foreground: #ffffff;
  --border: #e2e8f0;
  --input: #f1f5f9;
  --ring: #047857;
  --radius: 0.875rem;
  --brand-primary: #047857;
  --brand-secondary: #059669;
